a {
  color: #00bcd4;
  text-decoration: none;
}



ul li {
  list-style: square;
}

.home {
  background: url(../assets/images/website-bg-1.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 0;
  padding-top: 46.190225959%; /* (img-height / img-width * container-width) */
  /* (879 / 1903 * 100) */
}



.training-yard-img {
  background: url(../assets/images/Medieval_Training_Yard.png);
  background-repeat: no-repeat;
  background-size: 100%;
}

.shop-img {
  background: url(../assets/images/Shop.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.home-content {
  position: absolute;
  top: 0;
  width: 100%;
}

.logo-text {
  font-family: Zombie;
  font-size: 8rem;
  text-shadow: -1px 5px 0px #a52121;
  letter-spacing: 8px;
  padding-top: 1em;
  margin-bottom: 0;
}

.slogan {
  font-family: Zombie;
  text-shadow: 0px 3px 0px rgb(0, 0, 0);
}

.section-heading {
  font-family: Zombie;
  background: #000;
  text-shadow: 0px 3px 0px #a52121;
  letter-spacing: 3px;
}

.banner-text {
  background: #0000008a;
  padding: 5px;
}

.banner-mobile-extended {
  padding-top: 0;
  background: #000;
}

@media only screen and (max-width: 1366px) {
  .logo-text {
    padding-top: 0.25em;
    font-size: 3.5rem;
  }

  .slogan {
    font-size: 2rem;
  }
}

@media only screen and (max-width: 768px) {
  .logo-text {
    font-size: 3rem;
  }

  .slogan {
    font-size: 1.5rem;
  }

  .home {
    background-image: url(../assets/images/website-bg-1-mobile.png);
    padding-top: 95.8515283843%; /* (img-height / img-width * container-width) */
    /* (878 / 916 * 100) */
  }
}

.footer{
  position: relative;
}

.footer-image-left{
  position: absolute;
  left:0;
  bottom:0;
}

.footer-image-right{
  position: absolute;
  right:0;
  bottom:0;
}