.header{
    background:none;
}

.social-button svg{
    fill:white;
    padding-right: 10px;
    
}
.social-button{
    margin-right:15px;
}

.nav-anchor{
    color: white;
    text-decoration: none;
}

.menu-toggle{
    position:fixed;
    top: 10px;
    left: 20px;
}

@media only screen and (max-width: 768px) {
    .social-button{
        margin-right:0;
    }

    .menu-toggle{
        background-color:#000;
        box-shadow: 0px 0px 3px #fff;
    }
}